import React, { useState } from 'react';
import {
    Box, Typography, Container, Paper, TextField, Button, Grid,
    FormControl, InputLabel, Select, MenuItem, Alert, Chip, Divider,
    Breadcrumbs, Link, Snackbar, CircularProgress, Card, CardContent,
    useTheme
} from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import LightbulbIcon from '@mui/icons-material/Lightbulb';
import SendIcon from '@mui/icons-material/Send';
import NewReleasesIcon from '@mui/icons-material/NewReleases';
import BugReportIcon from '@mui/icons-material/BugReport';
import EnhancementIcon from '@mui/icons-material/AutoFixHigh';
import BuildIcon from '@mui/icons-material/Build';
import AppsIcon from '@mui/icons-material/Apps';
import LocalLibraryIcon from '@mui/icons-material/LocalLibrary';
import MosqueIcon from '@mui/icons-material/Mosque';
import PrayerIcon from '@mui/icons-material/AccessAlarm';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import GitHubIcon from '@mui/icons-material/GitHub';
import EmailIcon from '@mui/icons-material/Email';

const FeatureRequestPage = () => {
    const theme = useTheme();
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        category: '',
        title: '',
        description: '',
        platform: []
    });
    const [submitStatus, setSubmitStatus] = useState(null);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    
    const categories = [
        { value: 'prayer_times', label: 'Prayer Times', icon: <PrayerIcon fontSize="small" /> },
        { value: 'quran', label: 'Quran Features', icon: <LocalLibraryIcon fontSize="small" /> },
        { value: 'calendar', label: 'Hijri Calendar', icon: <CalendarMonthIcon fontSize="small" /> },
        { value: 'ui', label: 'User Interface', icon: <AppsIcon fontSize="small" /> },
        { value: 'bugs', label: 'Bug Report', icon: <BugReportIcon fontSize="small" /> },
        { value: 'other', label: 'Other', icon: <BuildIcon fontSize="small" /> }
    ];
    
    const platforms = [
        { value: 'web', label: 'Web App' },
        { value: 'android', label: 'Android App' },
        { value: 'ios', label: 'iOS App' },
        { value: 'alexa', label: 'Alexa Skill' },
        { value: 'api', label: 'API' }
    ];
    
    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevData => ({
            ...prevData,
            [name]: value
        }));
    };
    
    const handlePlatformChange = (platform) => {
        setFormData(prevData => {
            const platforms = [...prevData.platform];
            const platformIndex = platforms.indexOf(platform);
            
            if (platformIndex === -1) {
                platforms.push(platform);
            } else {
                platforms.splice(platformIndex, 1);
            }
            
            return {
                ...prevData,
                platform: platforms
            };
        });
    };
    
    const validateForm = () => {
        if (!formData.name || !formData.email || !formData.title || !formData.description) {
            setSubmitStatus({
                success: false,
                message: 'Please fill out all required fields.'
            });
            return false;
        }
        
        if (!formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
            setSubmitStatus({
                success: false,
                message: 'Please enter a valid email address.'
            });
            return false;
        }
        
        return true;
    };
    
    const handleSubmit = (e) => {
        e.preventDefault();
        
        if (!validateForm()) {
            setSnackbarOpen(true);
            return;
        }
        
        setIsSubmitting(true);
        
        // Always use the direct GitHub issue form approach
        const category = categories.find(cat => cat.value === formData.category)?.label || "Other";
        const platformList = formData.platform.length > 0 
            ? formData.platform.map(p => platforms.find(plat => plat.value === p)?.label).join(", ")
            : "Not specified";
            
        const issueTitle = `[Feature Request] ${formData.title}`;
        const issueBody = `
## Feature Request
**Submitted by:** ${formData.name}
**Email:** ${formData.email}
**Category:** ${category}
**Platforms:** ${platformList}

## Description
${formData.description}

---
*This feature request was submitted via the Salat-e-Mustaqeem Feature Request form.*
`;
        
        // URL encode parameters
        const params = new URLSearchParams({
            title: issueTitle,
            body: issueBody,
            labels: 'feature-request,' + formData.category
        }).toString();
        
        // Redirect to GitHub issue creation with prefilled info
        window.open(`https://github.com/imuslimlife/issues/new?${params}`, '_blank');
        
        // Show success message
        setSubmitStatus({
            success: true,
            message: 'Redirecting you to GitHub to complete your feature request submission.'
        });
        setSnackbarOpen(true);
        
        // Clear form
        setFormData({
            name: '',
            email: '',
            category: '',
            title: '',
            description: '',
            platform: []
        });
        
        setIsSubmitting(false);
    };
    
    const handleSnackbarClose = () => {
        setSnackbarOpen(false);
    };

    // Featured recent requests
    const featuredRequests = [
        {
            title: "Dark Mode for Mobile App",
            category: "ui",
            description: "Add a dark mode option to reduce eye strain at night",
            votes: 87,
            status: "in_progress"
        },
        {
            title: "Quran Audio with Variable Speed",
            category: "quran",
            description: "Allow users to control playback speed of Quran recitations",
            votes: 124,
            status: "planned"
        },
        {
            title: "Multiple Athan Voices",
            category: "prayer_times",
            description: "Add more athan voices including regional variations",
            votes: 156,
            status: "implemented"
        }
    ];

    const getStatusChip = (status) => {
        switch(status) {
            case 'implemented':
                return <Chip size="small" label="Implemented" color="success" />;
            case 'in_progress':
                return <Chip size="small" label="In Progress" color="primary" />;
            case 'planned':
                return <Chip size="small" label="Planned" color="secondary" />;
            default:
                return <Chip size="small" label="Under Review" color="default" />;
        }
    };
    
    const getCategoryIcon = (category) => {
        switch(category) {
            case 'prayer_times':
                return <PrayerIcon fontSize="small" sx={{ color: theme.palette.primary.main }} />;
            case 'quran':
                return <LocalLibraryIcon fontSize="small" sx={{ color: theme.palette.secondary.main }} />;
            case 'ui':
                return <AppsIcon fontSize="small" sx={{ color: theme.palette.info.main }} />;
            case 'bugs':
                return <BugReportIcon fontSize="small" sx={{ color: theme.palette.error.main }} />;
            default:
                return <BuildIcon fontSize="small" sx={{ color: theme.palette.text.secondary }} />;
        }
    };

    return (
        <>
            <Snackbar
                open={snackbarOpen}
                autoHideDuration={6000}
                onClose={handleSnackbarClose}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert 
                    onClose={handleSnackbarClose} 
                    severity={submitStatus?.success ? "success" : "error"}
                    variant="filled"
                    sx={{ width: '100%' }}
                >
                    {submitStatus?.message}
                </Alert>
            </Snackbar>

            <Box
                sx={{
                    background: `linear-gradient(135deg, ${theme.palette.primary.main}, ${theme.palette.primary.dark})`,
                    color: 'white',
                    pt: 8,
                    pb: 6,
                    position: 'relative'
                }}
            >
                <Container maxWidth="lg">
                    <Typography variant="h2" component="h1" gutterBottom fontWeight="500">
                        Feature Requests
                    </Typography>
                    <Typography variant="h6" sx={{ maxWidth: 800, mb: 3, opacity: 0.9 }}>
                        Suggest new features or improvements for Salat-e-Mustaqeem apps and help us enhance your experience
                    </Typography>
                    <Breadcrumbs aria-label="breadcrumb" sx={{ color: 'rgba(255, 255, 255, 0.7)' }}>
                        <Link 
                            component={RouterLink} 
                            to="/" 
                            sx={{ 
                                display: 'flex', 
                                alignItems: 'center',
                                color: 'inherit',
                                '&:hover': { color: 'white' } 
                            }}
                        >
                            <HomeIcon sx={{ mr: 0.5 }} fontSize="inherit" />
                            Home
                        </Link>
                        <Typography color="white" sx={{ display: 'flex', alignItems: 'center' }}>
                            <LightbulbIcon sx={{ mr: 0.5 }} fontSize="inherit" />
                            Feature Requests
                        </Typography>
                    </Breadcrumbs>
                </Container>
                
                {/* Wave SVG at the bottom */}
                <Box
                    sx={{
                        position: 'absolute',
                        bottom: -1,
                        left: 0,
                        width: '100%',
                        lineHeight: 0,
                        color: theme.palette.background.default
                    }}
                >
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 100">
                        <path
                            fill="currentColor"
                            fillOpacity="1"
                            d="M0,32L48,48C96,64,192,96,288,96C384,96,480,64,576,48C672,32,768,32,864,32C960,32,1056,32,1152,42.7C1248,53,1344,75,1392,85.3L1440,96L1440,320L1392,320C1344,320,1248,320,1152,320C1056,320,960,320,864,320C768,320,672,320,576,320C480,320,384,320,288,320C192,320,96,320,48,320L0,320Z"
                        />
                    </svg>
                </Box>
            </Box>

            <Container maxWidth="lg" sx={{ py: 6 }}>
                <Grid container spacing={4}>
                    {/* Featured Requests */}
                    <Grid item xs={12} md={4}>
                        <Typography variant="h5" gutterBottom sx={{ display: 'flex', alignItems: 'center', color: theme.palette.primary.main }}>
                            <NewReleasesIcon sx={{ mr: 1 }} />
                            Popular Requests
                        </Typography>
                        
                        <Box sx={{ mb: 4 }}>
                            {featuredRequests.map((request, index) => (
                                <Card 
                                    key={index} 
                                    sx={{ 
                                        mb: 2, 
                                        borderLeft: `4px solid ${
                                            request.status === 'implemented' ? theme.palette.success.main :
                                            request.status === 'in_progress' ? theme.palette.primary.main :
                                            theme.palette.secondary.main
                                        }` 
                                    }}
                                >
                                    <CardContent>
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 1 }}>
                                            <Typography variant="subtitle1" fontWeight="medium">
                                                {request.title}
                                            </Typography>
                                            {getStatusChip(request.status)}
                                        </Box>
                                        
                                        <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
                                            {getCategoryIcon(request.category)}
                                            <Typography variant="caption" color="text.secondary" sx={{ ml: 1 }}>
                                                {categories.find(cat => cat.value === request.category)?.label || 'Other'}
                                            </Typography>
                                        </Box>
                                        
                                        <Typography variant="body2" color="text.secondary" paragraph>
                                            {request.description}
                                        </Typography>
                                        
                                        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                            <Chip
                                                label={`${request.votes} votes`}
                                                size="small"
                                                variant="outlined"
                                                color="primary"
                                            />
                                        </Box>
                                    </CardContent>
                                </Card>
                            ))}
                            
                            <Box sx={{ textAlign: 'center', mt: 3 }}>
                                <Button 
                                    variant="outlined"
                                    color="primary"
                                    component={Link}
                                    href="https://github.com/imuslimlife/issues"
                                    target="_blank"
                                >
                                    View All Requests
                                </Button>
                            </Box>
                        </Box>
                        
                        {/* How It Works Section */}
                        <Box sx={{ mt: 6 }}>
                            <Paper elevation={1} sx={{ p: 3, bgcolor: 'background.default', borderRadius: 2 }}>
                                <Typography variant="h6" gutterBottom color="primary">
                                    How It Works
                                </Typography>
                                
                                <Typography variant="body2" paragraph>
                                    <strong>1. Submit Your Idea</strong> - Fill out the form with details about your feature request.
                                </Typography>
                                
                                <Typography variant="body2" paragraph>
                                    <strong>2. Community Review</strong> - Our community and team will review and possibly upvote your suggestion.
                                </Typography>
                                
                                <Typography variant="body2" paragraph>
                                    <strong>3. Development</strong> - Popular and feasible features get added to our development roadmap.
                                </Typography>
                                
                                <Typography variant="body2">
                                    <strong>4. Implementation</strong> - When completed, we'll release the feature in an app update.
                                </Typography>
                            </Paper>
                        </Box>
                    </Grid>
                    
                    {/* Request Form */}
                    <Grid item xs={12} md={8}>
                        <Paper elevation={3} sx={{ p: 4, borderRadius: 2 }}>
                            <Typography variant="h5" component="h2" gutterBottom sx={{ color: theme.palette.primary.main }}>
                                Submit a Feature Request
                            </Typography>
                            
                            <Typography variant="body2" color="text.secondary" paragraph>
                                Have an idea to improve Salat-e-Mustaqeem? We'd love to hear it! Fill out the form below with the details of your suggestion.
                            </Typography>
                            
                            <Alert severity="info" sx={{ mb: 3 }}>
                                Your request will open a pre-filled GitHub issue form where you can review and submit.
                            </Alert>

                            {/* Add this new section for alternative submission options */}
                            <Box sx={{ mb: 3, display: 'flex', flexDirection: 'column', gap: 1 }}>
                                <Typography variant="subtitle2">
                                    Alternative submission methods:
                                </Typography>
                                <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap' }}>
                                    <Button
                                        variant="outlined"
                                        size="small"
                                        startIcon={<GitHubIcon />}
                                        href="https://github.com/imuslimlife/issues/new"
                                        target="_blank"
                                        rel="noopener noreferrer"
                                    >
                                        Create Issue Manually
                                    </Button>
                                    <Button
                                        variant="outlined"
                                        size="small"
                                        startIcon={<EmailIcon />}
                                        href="mailto:info@yzinfotech.com?subject=Feature%20Request"
                                        target="_blank"
                                        rel="noopener noreferrer"
                                    >
                                        Email Request
                                    </Button>
                                </Box>
                            </Box>
                            
                            {submitStatus?.success && (
                                <Alert severity="success" sx={{ mb: 3 }} action={
                                    submitStatus.issueUrl && (
                                        <Button 
                                            color="inherit" 
                                            size="small"
                                            href={submitStatus.issueUrl}
                                            target="_blank"
                                            rel="noopener noreferrer"
                                        >
                                            View on GitHub
                                        </Button>
                                    )
                                }>
                                    {submitStatus.message}
                                </Alert>
                            )}
                            
                            <form onSubmit={handleSubmit}>
                                <Grid container spacing={3}>
                                    {/* Personal Information */}
                                    <Grid item xs={12} sm={6}>
                                        <TextField
                                            required
                                            fullWidth
                                            label="Your Name"
                                            name="name"
                                            value={formData.name}
                                            onChange={handleChange}
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={12} sm={6}>
                                        <TextField
                                            required
                                            fullWidth
                                            label="Email Address"
                                            name="email"
                                            type="email"
                                            value={formData.email}
                                            onChange={handleChange}
                                            variant="outlined"
                                            helperText="We'll notify you about updates to your request"
                                        />
                                    </Grid>
                                    
                                    {/* Request Details */}
                                    <Grid item xs={12}>
                                        <FormControl fullWidth required>
                                            <InputLabel>Category</InputLabel>
                                            <Select
                                                name="category"
                                                value={formData.category}
                                                onChange={handleChange}
                                                label="Category"
                                            >
                                                {categories.map(category => (
                                                    <MenuItem key={category.value} value={category.value}>
                                                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                                            {category.icon}
                                                            <Typography sx={{ ml: 1 }}>{category.label}</Typography>
                                                        </Box>
                                                    </MenuItem>
                                                ))}
                                            </Select>
                                        </FormControl>
                                    </Grid>
                                    
                                    <Grid item xs={12}>
                                        <TextField
                                            required
                                            fullWidth
                                            label="Feature Title"
                                            name="title"
                                            value={formData.title}
                                            onChange={handleChange}
                                            variant="outlined"
                                            placeholder="A short, descriptive title for your feature request"
                                        />
                                    </Grid>
                                    
                                    <Grid item xs={12}>
                                        <TextField
                                            required
                                            fullWidth
                                            multiline
                                            rows={4}
                                            label="Description"
                                            name="description"
                                            value={formData.description}
                                            onChange={handleChange}
                                            variant="outlined"
                                            placeholder="Please describe your feature request in detail. What problem would it solve? How should it work?"
                                        />
                                    </Grid>
                                    
                                    {/* Platform Selection */}
                                    <Grid item xs={12}>
                                        <Typography variant="subtitle2" gutterBottom>
                                            Which platforms should this feature be added to? (Optional)
                                        </Typography>
                                        <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                                            {platforms.map(platform => (
                                                <Chip
                                                    key={platform.value}
                                                    label={platform.label}
                                                    onClick={() => handlePlatformChange(platform.value)}
                                                    color={formData.platform.includes(platform.value) ? "primary" : "default"}
                                                    variant={formData.platform.includes(platform.value) ? "filled" : "outlined"}
                                                    clickable
                                                />
                                            ))}
                                        </Box>
                                    </Grid>
                                    
                                    <Grid item xs={12}>
                                        <Divider sx={{ my: 1 }} />
                                        <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
                                            <Button
                                                type="submit"
                                                variant="contained"
                                                color="primary"
                                                size="large"
                                                disabled={isSubmitting}
                                                endIcon={isSubmitting ? <CircularProgress size={20} color="inherit" /> : <SendIcon />}
                                            >
                                                {isSubmitting ? 'Submitting...' : 'Submit Feature Request'}
                                            </Button>
                                        </Box>
                                    </Grid>
                                </Grid>
                            </form>
                        </Paper>
                    </Grid>
                </Grid>
            </Container>
        </>
    );
};

export default FeatureRequestPage;